// 这个文件是创建vuex数据状态管理的场所
import Vue from 'vue'
import Vuex from 'vuex'

// 引入本地存储的方法
import { getItem, setItem } from '../utils/storage.js'
Vue.use(Vuex)

// 所有的数据内容需要写在下面的实例化对象中
export default new Vuex.Store({
  // 存储数据的地方 ------ 类似于vue中的data()
  state: {
    // 存储的是一个对象，存储当前登录用户的token数据
    /* 没有封装storage的写法：
      user: JSON.parse(window.localStorage.getItem('TOKEN_KFY')) */
    user: getItem('TOKEN_KFY')
  },
  // 外界修改store中state属性的途径，必须通过mutations设置的方法来修改state  ---类似于methods
  mutations: {
    setUser (state, data) {
      /*
      state:vuex中的state里面存放的值，data是外界传递到这个方法中的参数
      */
      state.user = data
      // 为了防止刷新时数据丢失，需要把数据备份到本地存储中
      /*  没有封装storage的写法：
      window.localStorage.setItem('TOKEN_KFY', JSON.stringify(state.user)) */
      setItem('TOKEN_KFY', state.user)
    }
  },
  // 直接当做state的计算属性 ------类似于computed
  getters: {
  },
  // 涉及到异步操作后修改state数据时，（需要在vueX中请求接口时，才会去修改state时）就必须通过axtions中定义的方法来调用mutations中修改state的方法
  actions: {
  },
  // 模块化vuex，可以让每一个模块都拥有直接的state,mutations,actions,getters,让结构更清晰
  modules: {
  }
})
/*
从store状态管理器中获取数据的两种方式
1.this.$store.state.数据
2.computed：{
  ...mapState(["数据"])
}
两种方式如何选择：如果一个页面中频繁的会使用到state中的某几个属性时，使用方法2，可以减少描述属性的代码  ----例channel-edit.vue
虽然会用到各种state中定义的变量，但是每个变量的使用频率并不频繁，使用方法1
*/
