// 固定写法
// 引入dayjs
import dayjs from 'dayjs'

// 引入vue
import Vue from 'vue'

// 加载中文语言包
// 如果要引用node_modules里的第三方依赖，禁止使用相对路径和绝对路径。直接写依赖包名字即可
import 'dayjs/locale/zh-cn'

// 尝试获取当前时间的格式化后的内容
// console.log(dayjs().format('YYYY-MM-DD HH:mm:ss'))

// 在main.js中移入import './utils/dayjs.js'

// 配置使用相对时间的插件
import relativeTime from 'dayjs/plugin/relativeTime.js'
// 配置使用处理相对时间的插件
dayjs.extend(relativeTime)

// dayjs的默认语言是英文
dayjs.locale('zh-cn')
// 使用全局过滤器制作
Vue.filter('relativeTime', function (value) {
  // 通过dayjs(目标时间).to(dayjs(当前时间))方法来展示的xx年前
  // 通过dayjs(当前时间).to(dayjs(目标时间))方法来展示的xx年内
  return dayjs().to(dayjs(value))
})
